import React from "react";
import styles from "./ShowExperienceResume.module.css";

const ShowExperienceResume = (props) => {
  const experiences = [
    {
      date: "Since 2022",
      title: "CO-FOUNDER & FULL-STACK DEVELOPER: Mostaamel.tn",
      label:
        "Launch web application called Mostaamel that allows its members to make donations or to collect them online.",
    },
    {
      date: "Jul- Sept 2022",
      title: "Summer INTERNSHIP: Neocortex",
      label:
        "Design and development of a web application for managing the absences in a company.",
    },
    {
      date: "June 2022",
      title: "« Bal des Projets »",
      label: `Participation in the 9th edition of the « Bal des Projets » organized by ESPRIT, presents the best projects developed by the students of the University.`,
    },
    {
      date: "Jul-Sept 2021",
      title: "Freelance",
      label: "Development of an election website for one of my clients.",
    },

    {
      date: "Feb-June 2021",
      title: "Internship: One Gate Africa",
      label:
        "Development and implementation of a web application for private transportation rentals.",
    },
    {
      date: "December 2019",
      title: "Problem Solving Coach",
      label:
        "Participation in organizing committee for both the TOP and the WinterCamp which is hosted by Informatics Tunisian Association.",
    },
    {
      date: "November 2019",
      title: "IEEEXtreme",
      label: "IEEE extreme programming solving competition.",
    },
    {
      date: "September 2019",
      title: "TCPC Finals",
      label:
        "Participation in ACM Tunisian Collegiate Programming Contest Finals (Team: Puzzles).",
    },
    {
      date: "March 2019",
      title: "ISI CPC Qualifications",
      label:
        "Participation in ACM Tunisian Collegiate Programming Contest Qualifications (Team: Puzzles).",
    },
    {
      date: "March 2017",
      title: "TOP Finals",
      label: "Participation in Tunisian Olympiad Programming Finals (Solo).",
    },
  ];
  return (
    <ul className={`${styles.List} list`}>
      {experiences.map((experience) => {
        return (
          <li key={experience.title}>
            <span></span>
            <div className={`${styles.Media} media`}>
              <div className={`${styles.dFlex} d-flex`}>
                <p>{experience.date}</p>
              </div>
              <div className={`${styles.mediaBbody} media-body`}>
                <h4>{experience.title}</h4>
                <p>{experience.label}</p>
              </div>
            </div>
          </li>
        );
      })}
    </ul>
  );
};

export default ShowExperienceResume;
